.locationPage{
    margin: 10% auto;
    width: 70%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.locationhead{
    display: block;
    color: rgba(74, 74, 245, 0.733);
}

.locationIcon{
    width: 30%;
    text-align: center;
}

.rightSide{
    width: 60%;
    margin: 2% auto;
}

.locationForm{
    display: flex;
    justify-content: space-around;
    align-items: center;
    border: 1px solid black;
    padding: 2%;
    background-color: #EAE4D5;
}

.locationPageLabel{
    font-size: x-large;
}

.locationPageInp{
    width: 50%;
    border-radius:15px ;
    font-size: large;
    padding: 1%;
}

.locationPageBtn{
    padding: 2%;
    border-radius: 25px;
    font-size: medium;
    background-color: black;
    color: aqua;
    cursor: pointer;
}

.backBtnDiv{
    width: 40%;
    margin: 2% auto;
}

.backBtn{
    padding: 2%;
    width: 100%;
    border-radius: 5px;
    font-size: medium;
    background-color: black;
    color: gold;
    cursor: pointer;
}
