.welcomePage{
    /* width: 80%; */
    margin: 4% auto;
    background-image: url("https://thumbs.dreamstime.com/b/two-hands-present-red-first-aid-kit-marked-white-cross-against-pastel-blue-backdrop-greenery-holding-background-348886834.jpg");
    background-size: cover;
    width: 100vw;
    height: 100vh;
}

.welcomePageHead{
    text-align: center;
    color: red;
    font-family: 'Times New Roman', Times, serif;
    font-size: xx-large;
    font-weight: bold;
}

.welcomePageTagline{
    text-align: center;
    font-size: x-large;
    font-family: 'Times New Roman', Times, serif;
    color: #FF7F3E;
}

.introParas{
    width: 50%;
    float: right;
    margin: 5%;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: larger;
    line-height: 1.6;
}

.para1,.para2{
    margin: 6% auto;
}

.para1{
    text-align: center;
    color: #0C0950;
}

.para2{
    font-size: x-large;
    font-style: italic;
    color: #D20062;
}