/* HOME PAGE */

.homePage{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 5% 0%;
}

.pageLeftSection{
    width: 60%;
}

.pageRightSection{
    width: 15%;
    
}


/* WEATHER APP */

.weatherSection{

    display: block;
    position: fixed;
    z-index: -1;
    top:7%;
    

    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: large;
    padding: 2%;
    border-radius: 5px;
    border: 7px inset #03A6A1;;
    text-align: center;
    background-color: #ffe3bb98;

    
}

.weatherBottomSection{
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.largeLabel{
    font-size: xx-large;
}

.locationSection{
    margin: 10% 0%;
    font-size: medium;
}


/* FORM PAGE CSS */

.formContainer{
    font-size: larger;
    border: 1px solid red;
    box-shadow: 20px 20px 10px 0px rgba(247, 74, 6, 0.726);
}

.formHead{
    font-weight: bold;
    color: blue;
    font-size: x-large;
    
}

.formInputDiv{
    display: flex;
    align-items: center;
    margin: 2% 0%;
}

.formInputDiv label{
    flex: 1;
}

.formInputDiv input, .formInputDiv textarea, .formInputDiv .p-rating{
    flex: 2;
    padding: 1%;
    border-radius: 10px;
}

.submitBtn{
    border-radius: 5px;
    padding: 1%;
    background-color: #000000;
    color: #FFFCFB;
    font-weight: bold;
    cursor: pointer;
    font-size: medium;
    margin: 1% auto;
}

:global(.p-rating){
     margin-right: 8px;
}

/* Form Response */
.responsePage{
    font-size: large;
    margin: 10% auto;
}


.test{
    margin: 20%;
}