.navbar{
    background-color: #A7C1A8;
    display:flex;
    justify-content: space-around;
    align-items: center;
    padding: 0.5%;
    position: fixed;
    top: 0;
    width: 100%;
    height: 5%;
}

.navHead{
    background-color: transparent;
    border: none;
    cursor: pointer;
    width: 20%;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight: bold;
    font-size: xx-large;
    color: #BF3131;
}

.navLinks{
    width: 40%;
    display:flex;
    justify-content: space-around;
    align-items: center;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: large;
    font-weight: bold;
}

.navItems{
    text-decoration: none;
    color: #8D0B41;
}

.profileName{
    background-color: transparent;
    border: none;
    cursor: pointer;
    width: 7%;
    height: 100%;
    font-size: medium;
    font-style: italic;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
