.checkListPage{
    width: fit-content;
}

.checkListBox{
    margin: 0%;
    border: 12px double #FF2DD1;
    background-color: #FED2E2;
    padding: 2%;
}

.checkListCircle{
    font-size: xx-large;
    text-align: center;
    margin: 0%;
    color: #0065F8;
}

.checkListHead{
    font-size: x-large;
    color: #4E31AA;
    text-align: center;
    font-style: italic;
    
}

.listItem{
    margin: 2.5% auto;
    font-size: large;
}

.updateBtn{
    padding: 2%;
    border-radius: 20px;
    background-color: black;
    color: white;
    cursor: pointer;
    font-weight: bold;
}

.checkListInp{
    cursor: pointer;
}